import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router";

import { withStyles } from '@material-ui/core';
import { Autocomplete } from '@material-ui/lab';
import { FormControl, Select, MenuItem, FormHelperText, Chip, TextField, InputAdornment, Paper, Tooltip, Button, Grid, Link } from '@material-ui/core';
import { Search } from '@material-ui/icons';

import matchSorter from 'match-sorter';

import { searchResultsFetch, keywordsFetch } from '../../Redux/actions/catalog';

import colors from '../../Enums/colors';

import uriEncodeSearchQuery from '../../Utility/Catalog/uriEncodeSearchQuery';

const mapStateToProps = (state, ownProps) => ({
    keywords: state.keywords,
    searchResults: state.searchResults
})

const mapDispatchToProps = {
    keywordsFetch
}

const styles = (theme) => ({
    
    autoCompleteWrapper: {
        position: '-webkit-sticky',
        position: 'sticky',
        top: '152px',
        padding: '14px 20px'
    },

    chip: {
        maxWidth: '160px',
        color: 'white',
        borderColor: theme.palette.primary.main,
        marginRight: '6px',
        height: '28px'
    },

    searchButton: {
        // color: '#2E3D19',
        // margin: '0 0 -20px 16px',
        textTransform: 'none',
        width: '160px',
        height: '37px'
    },

    autocompletePopperPaper: {
        backgroundColor: '#1F4B65'
    },

    depthFormControlRoot: {
        textAlign: 'left'
    }
});

const filterOptions = (options, { inputValue }) => {
    let values = inputValue.replace('_',' ').split(' ');
    let result = options;

    values.forEach(value => {
        result = matchSorter(result, value);
    });

    return result.sort((a,b) => a.length - b.length);
}

const searchRef = React.createRef();

const CatalogSearch = (props) => {
    const { classes, keywordsFetch, keywords } = props;

    const [ inputValue, setInputValue ] = useState('');
    const [ searchWords, setSearchWords ] = useState([]);
    const [ hasDepth, setHasdepth ] = useState('any');
    const [ startTime, setStartTime ] = useState('');
    const [ endTime, setEndTime ] = useState('');

    console.log(props.history)

    useEffect(() => {
        if(!keywords || !keywords.length){
            keywordsFetch();
        }
    }, []);

    const handleSearch = () => {
        props.history.push(`/catalog?${uriEncodeSearchQuery({keywords: searchWords, hasDepth, startTime, endTime})}`);
    }

    const handleValueChange = (e, value) => {
        setSearchWords(value);
        props.history.push(`/catalog?${uriEncodeSearchQuery({keywords: value, hasDepth, startTime, endTime})}`)
    }

    const handleDepthChange = (e) => {
        setHasdepth(e.target.value);
        props.history.push(`/catalog?${uriEncodeSearchQuery({keywords: searchWords, hasDepth: e.target.value, startTime, endTime})}`);
    }

    const handleKeyDown = (e) => {
        if(e.which === 13 && !inputValue) searchRef.current.click();
    }

    return (        
        <Paper className={classes.autoCompleteWrapper} elevation={4}>
            <Grid container justify='center'>             
                <Grid item xs={12}>
                        <Autocomplete
                            onKeyDown={handleKeyDown}
                            value={searchWords}
                            onChange={handleValueChange}
                            clearOnBlur
                            size='small'
                            classes={{
                                paper: classes.autocompletePopperPaper
                            }}
                            fullWidth={false}
                            filterOptions={filterOptions}
                            handleHomeEndKeys={true}
                            open={inputValue.length > 1}
                            multiple
                            id="catalog-search-autocomplete"
                            options={keywords}
                            clearOnEscape
                            freeSolo
                            inputValue={inputValue}
                            onInputChange={(e, value) => setInputValue(value)}
                            renderTags={(value, getTagProps) =>
                                value.map((option, index) => (
                                    <Tooltip title={option} key={option}>
                                        <Chip variant='outlined' label={option} {...getTagProps({ index })} className={classes.chip}/>
                                    </Tooltip>
                                ))
                            }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    placeholder={searchWords.length > 0 ? 'Addt\'l Search Terms' : 'Search The Dataset Catalog'}
                                    InputProps={{
                                        ...params.InputProps,
                                        startAdornment: (
                                            <React.Fragment>
                                                <InputAdornment position="start">
                                                    <Search style={{color: colors.primary}}/>
                                                </InputAdornment>
                                                {params.InputProps.startAdornment}
                                            </React.Fragment>
                                        )
                                    }}
                                    variant="outlined"                            
                                />
                            )}
                        />

                        <Grid item xs={12} style={{marginTop: '40px'}}>
                            <FormControl>
                            <Select
                                variant='outlined'
                                value={hasDepth}
                                onChange={handleDepthChange}
                            >
                                <MenuItem value='any'>Any</MenuItem>
                                <MenuItem value='yes'>Multiple</MenuItem>
                                <MenuItem value='no'>Surface Only</MenuItem>
                            </Select>
                            <FormHelperText>Depth Levels</FormHelperText>
                        </FormControl>
                        </Grid>                       

                        <Grid item container xs={12} style={{marginTop: '40px'}}>
                            <Grid item xs={6}>
                                <TextField
                                    label='Start Date'
                                    value={startTime}
                                    onChange={(e, v) => setStartTime(v)}
                                    placeholder='YYYY-MM-DD'
                                    variant='outlined'
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                        </Grid>

                        <Grid item xs={6}>
                            <TextField
                                label='End Date'
                                value={endTime}
                                onChange={(e, v) => setEndTime(v)}
                                placeholder='YYYY-MM-DD'
                                variant='outlined'
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />
                        </Grid>

                        </Grid>

                            

                        <Grid item xs={12} style={{height: '20px', margin: '24px 0', padding: '9px 0 0 28px'}}>
                            <Button 
                                disabled={!Boolean(keywords && keywords.length)} 
                                color='primary' variant='outlined' 
                                className={classes.searchButton} 
                                onClick={handleSearch}
                                ref={searchRef}
                            >
                                    Search Datasets
                            </Button>
                        </Grid>
                    </Grid>
                </Grid>
            </Paper>            
            )
    }

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(withRouter(CatalogSearch)));



