import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { withStyles, Accordion, AccordionSummary, AccordionDetails, Typography, ListItem, List, ListItemText, Grid, Paper, Divider, Link, Table, TableRow, TableHead, TableCell, TableBody } from '@material-ui/core';
import { Skeleton } from '@material-ui/lab';
import { ExpandMore } from '@material-ui/icons';

import ReactMarkdown from 'react-markdown';

import { datasetFullPageDataFetch, datasetFullPageDataStore } from '../../Redux/actions/catalog';

import states from '../../Enums/asyncRequestStates';
import colors from '../../Enums/colors';


const mapStateToProps = (state, ownProps) => ({
    datasetFullPageDataLoadingState: state.datasetFullPageDataLoadingState,
    datasetFullPageData: state.datasetFullPageData
})

const mapDispatchToProps = {
    datasetFullPageDataFetch,
    datasetFullPageDataStore
}

const styles = (theme) => ({
    stickyPaper: {
        position: '-webkit-sticky',
        maxHeight: 'calc(100vh - 128px)',
        position: 'sticky',
        top: '90px',
        width: '200px',
        marginLeft: '20px',
        paddingLeft: '12px',
        backgroundColor: 'rgba(0,0,0,.4)',
        overflow: 'auto'
    },

    guideSection: {
        width: '90%',
        margin: '20px auto 0 auto',
        textAlign: 'left',
        padding: '12px 32px',
        fontFamily: '"roboto", Serif',
        maxWidth: '800px',
        backgroundColor: 'rgba(0,0,0,.4)',
        marginBottom: '20px'
    },

    sectionHeader: {
        margin: '16px 0 2px 0',
        fontWeight: 100,
        fontFamily: '"roboto", Serif', 
    },

    '@media screen and (max-width: 1300px)': {
        stickyPaper: {
          display: 'none',
        },
    },

    navListItem: {
        color: theme.palette.primary.main,
        padding: '2px 10px 2px 6px'
    },

    navListItemText: {
        '&:hover': {
            textDecoration: 'underline'
        }
    },

    doiListItem: {
        color: theme.palette.primary.main,
        padding: '0 10px 0 6px',
        width: 'max-content'
    },

    doiListItemText: {
        fontSize: '16px',
        '&:hover': {
            textDecoration: 'underline'
        }
    },

    doiListItemtextWrapper: {
        margin: '0'
    },

    navListItemtextWrapper: {
        margin: '2px 0'
    },

    subListText: {
        margin: 0,
        '&:hover': {
            textDecoration: 'underline'
        }
    },

    anchor: {
        display: 'block',
        position: 'relative',
        top: '-120px',
        visibility: 'hidden'
    },

    divider: {
        backgroundColor: theme.palette.primary.main,
        marginBottom: '8px'
    },

    sampleTableRow: {
        '& td':{
            padding: '10px 24px 10px 16px'
        }
    },

    navListSubItemText: {
        fontSize: '.785rem'
    },

    navListSubSubItemText: {
        fontSize: '.7rem'
    },

    outerContainer: {
        marginTop: '70px'
    },

    markdown: {
        '& img': {
            maxWidth: '100%',
            margin: '20px auto 20px auto',
            display: 'block'
        },

        '& a': {
            color: theme.palette.primary.main,
            textDecoration: 'none'
        },

        '& p': {
            fontSize: '1rem',
            fontFamily: '"Lato",sans-serif',
            fontWeight: 400,
            lineHeight: 1.5
        }
    },

    smallText: {
        fontSize: '.8rem'
    },

    tableHead: {
        fontWeight: 600
    }
});

const SkeletonWrapper = (props) => {
    return props.loading ?
    (
        <React.Fragment>
            {props.children.map((child, i) => (
                <Skeleton key={i}>
                    {child}
                </Skeleton>
            ))}
        </React.Fragment>
    )

    :

    (
        <React.Fragment>
            {props.children.map((child, i) => (
                <React.Fragment key={i}>
                    {child}
                </React.Fragment>
            ))}
        </React.Fragment>
    )
}

const DatasetFullPage = (props) => {
    const { classes, datasetFullPageDataFetch, datasetFullPageDataStore, datasetFullPageData, datasetFullPageDataLoadingState } = props;

    const { 
        Variables,
        Acknowledgement,
        Data_Source,
        Depth_Max,
        Depth_Min,
        Description,
        Distributor,
        Lat_Max,
        Lat_Min,
        Lon_Max,
        Lon_Min,
        Long_Name,
        Short_Name,
        Table_Name,
        Time_Max,
        Time_Min,
        References,
        Make,
        Process_Level,
        Spatial_Resolution,
        Temporal_Resolution
    } = datasetFullPageData;

    const loading = datasetFullPageDataLoadingState === states.inProgress;

    useEffect(() => {
        datasetFullPageDataFetch(props.match.params.dataset);

        return (() => datasetFullPageDataStore({}));
    }, []);

    useEffect(() => {
        document.title = Long_Name;

        return (() => document.title = 'Simons Collaborative Marine Ocean Atlas')
    }, [Long_Name])

    return (
        <Grid container className={classes.outerContainer}>
            <Grid item xs={2}>
                <Paper className={classes.stickyPaper} elevation={6}>
                    <List dense={true}>
                        <ListItem component='a' href='#description' className={classes.navListItem}>
                            <ListItemText
                                primary="Description"
                                classes={{primary: classes.navListItemText}}
                                className={classes.navListItemtextWrapper}
                            />
                        </ListItem>

                        <ListItem component='a' href='#coverage' className={classes.navListItem}>
                            <ListItemText
                                primary="Coverage"
                                classes={{primary: classes.navListItemText}}
                                className={classes.navListItemtextWrapper}
                            />
                        </ListItem>

                        <ListItem component='a' href='#variables' className={classes.navListItem}>
                            <ListItemText
                                primary="Variables"
                                classes={{primary: classes.navListItemText}}
                                className={classes.navListItemtextWrapper}
                            />
                        </ListItem>

                        {
                            Distributor ?
                            <ListItem component='a' href='#distributor' className={classes.navListItem}>
                                <ListItemText
                                    primary="Distributor"
                                    classes={{primary: classes.navListItemText}}
                                    className={classes.navListItemtextWrapper}
                                />
                            </ListItem> : ''                 
                        }

                        {
                            Acknowledgement ?
                            <ListItem component='a' href='#acknowledgement' className={classes.navListItem}>
                                <ListItemText
                                    primary="Acknowledgement"
                                    classes={{primary: classes.navListItemText}}
                                    className={classes.navListItemtextWrapper}
                                />
                            </ListItem> : ''
                        }

                        {
                        References && References.length ? 
                            <ListItem component='a' href='#references' className={classes.navListItem}>
                                <ListItemText
                                    primary="References"
                                    classes={{primary: classes.navListItemText}}
                                    className={classes.navListItemtextWrapper}
                                />
                            </ListItem> : ''
                        }
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={8}>
                <Paper className={classes.guideSection} elevation={4}>
                    <SkeletonWrapper loading={loading}>
                        <Typography variant={'h4'}>
                            {Long_Name}                    
                        </Typography>

                        <ReactMarkdown source={Description} className={classes.markdown}/>

                        <a className={classes.anchor} id='coverage'></a>

                        <Table size='small' style={{marginTop: '24px'}}>
                            <TableBody>
                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Make
                                    </TableCell>
                                    <TableCell>
                                        {Make}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Process Level
                                    </TableCell>
                                    <TableCell>
                                        {Process_Level}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Temporal Resolution
                                    </TableCell>
                                    <TableCell>
                                        {Temporal_Resolution}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Time Start*
                                    </TableCell>
                                    <TableCell>
                                        {Time_Min ? Time_Min.slice(0, 10) : 'NA'}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Time End*
                                    </TableCell>
                                    <TableCell>
                                        {Time_Max ? Time_Max.slice(0, 10) : 'NA'}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Spatial Resolution
                                    </TableCell>
                                    <TableCell>
                                        {Spatial_Resolution}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Lat Start*
                                    </TableCell>
                                    <TableCell>
                                        {Lat_Min}&deg;
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Lat End*
                                    </TableCell>
                                    <TableCell>
                                        {Lat_Max}&deg;
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Lon Start*
                                    </TableCell>
                                    <TableCell>
                                        {Lon_Min}&deg;
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Lon End*
                                    </TableCell>
                                    <TableCell>
                                        {Lon_Max}&deg;
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Depth Start*
                                    </TableCell>
                                    <TableCell>
                                        {Depth_Max ? Depth_Min + 'm' : 'Surface Only'}
                                    </TableCell>
                                </TableRow>

                                <TableRow className={classes.sampleTableRow}>
                                    <TableCell className={classes.tableHead}>
                                        Depth End*
                                    </TableCell>
                                    <TableCell>
                                        {Depth_Max ? Depth_Max + 'm' : 'Surface Only'}
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                        <Typography variant='caption' style={{margin: '4px 0 14px 4px', display: 'inline-block'}}>
                            *Temporal and spatial coverage may differ between member variables
                        </Typography>

                        <Typography variant='h5' className={classes.sectionHeader} style={{marginBottom: '16px'}}>
                            <a className={classes.anchor} id='variables'></a>
                            Variables
                        </Typography>

                        {Variables ? Variables.map((variable, i) => (
                            <Accordion key={i}>
                                <AccordionSummary expandIcon={<ExpandMore/>}>
                                    <Typography className={classes.heading}>{variable.Variable}</Typography>
                                </AccordionSummary>
                                <AccordionDetails style={{display: 'block'}}>
                                    {
                                        variable.Variable === variable.Long_Name ? '' :
                                        <Typography className={classes.smallText}>
                                            {variable.Long_Name}
                                        </Typography>
                                    }

                                    {Object.keys(variable).map((e, i) => i < 2 ? '' : (
                                        <Typography className={classes.smallText} key={i}>
                                            <strong>{e}</strong>: {variable[e]}
                                        </Typography>
                                    ))}
                                    
                                </AccordionDetails>
                            </Accordion>  
                        )) : ''}                                              

                        {
                            Distributor || loading ?

                            <React.Fragment>
                                <Typography variant='h5' className={classes.sectionHeader}>
                                    <a className={classes.anchor} id='distributor'></a>
                                    Distributor
                                </Typography>

                                <Typography className={classes.smallText}>
                                    {Distributor}
                                </Typography>
                            </React.Fragment>


                            : ''
                        }

                        {Acknowledgement || loading ?
                        
                            <React.Fragment>
                                    <Typography variant='h5' className={classes.sectionHeader}>
                                        <a className={classes.anchor} id='acknowledgement'></a>
                                        Acknowledgement
                                    </Typography>

                                    <Typography className={classes.smallText}>
                                        {Acknowledgement}
                                    </Typography>
                                </React.Fragment>


                                : ''                        
                        }

                        {(References && References.length) || loading ?
                        
                            <React.Fragment>
                                    <Typography variant='h5' className={classes.sectionHeader}>
                                        <a className={classes.anchor} id='acknowledgement'></a>
                                        References
                                    </Typography>

                                    {!loading ?
                                        References.map((reference, i) => (
                                            <Typography className={classes.smallText} key={i}>
                                                {reference}
                                            </Typography>
                                        )) : ''}                            
                            </React.Fragment>

                            : ''
                        }
                    
                    </SkeletonWrapper>                    
                </Paper>
            </Grid>
        </Grid>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(DatasetFullPage));

{/* <Divider variant='fullWidth' classes={{root: classes.divider}}/> */}