import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router";

import { withStyles, Paper, Typography } from '@material-ui/core';

import CatalogSearch from './CatalogSearch';
import SearchResult from './SearchResult';

import { searchResultsFetch, searchResultsStore } from '../../Redux/actions/catalog';

const mapStateToProps = (state, ownProps) => ({
    searchResults: state.searchResults
})

const mapDispatchToProps = {
    searchResultsFetch,
    searchResultsStore
}

const styles = (theme) => ({
    wrapperDiv: {
        padding: '0 20px 20px 20px',
        boxSizing: 'border-box',
        overflow: 'hidden'
      },
    
      root: {
        width: '60vw',
        maxWidth: '1200px',
        minWidth: '480px',
        padding: '16px 24px',
        margin: '8px auto 24px auto',
    },

    resultsWrapper: {
        width: '60vw',
        maxWidth: '1200px',
        minWidth: '480px',
        padding: '16px 24px',
        margin: '8px auto 24px auto',
        backgroundColor: 'transparent'
    },

    resultsCount: {
        marginTop: '-6px',
        textAlign: 'left',
        marginBottom: '24x'
    }
});

const SearchResults = (props) => {
    const { classes, searchResults, searchResultsFetch, searchResultsStore } = props;

    useEffect(() => {
        searchResultsFetch(props.location.search);
    }, [props.location.search]);

    return (
        <div className={classes.wrapperDiv}>
            <Paper className={classes.resultsWrapper} elevation={0}>
                <Typography className={classes.resultsCount}>
                    Found {searchResults.length} datasets:
                </Typography>

                <div style={{minHeight: '70vh'}}>
                    {
                        searchResults.map(result => <SearchResult dataset={result} key={result.Short_Name}/>)
                    }
                </div>

            </Paper>
        </div>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(withRouter(SearchResults)));